import { Component, Prop, State, Event, EventEmitter, Watch, h, Host } from '@stencil/core';
import { MonthYear, DisabledMonth } from '../bh-month-picker-content/bh-month-picker-content';

export type MonthPickerVariant = 'dropdown-outlined' | 'dropdown-ghost' | 'input';

@Component({
  tag: 'bh-month-picker',
  styleUrl: 'bh-month-picker.css',
  shadow: true,
})
export class BhMonthPicker {
  /**
   * Variant of the month picker trigger
   */
  @Prop() variant: MonthPickerVariant = 'dropdown-outlined';

  /**
   * Minimum year in the picker
   */
  @Prop() minYear: number = 1998;

  /**
   * Maximum year in the picker
   */
  @Prop() maxYear: number = 2060;

  /**
   * Whether the picker is disabled
   */
  @Prop() disabled: boolean = false;

  /**
   * Array of disabled months
   */
  @Prop() disabledMonths: DisabledMonth[] = [];

  /**
   * Label for input variant
   */
  @Prop() label: string = 'Select Month';

  /**
   * Placeholder text
   */
  @Prop() placeholder: string = 'Choose month and year';

  /**
   * Width for input variant
   */
  @Prop() inputWidth: string = '200px';

  /**
   * Whether to show the label
   */
  @Prop() showLabel: boolean = true;

  /**
   * Whether to show the footer
   */
  @Prop() showFooter: boolean = true;

  /**
   * Currently selected value
   */
  @Prop() value?: MonthYear;

  @State() selectedValue: MonthYear | null = null;
  @State() tempSelection: MonthYear | null = null;
  @State() isMenuOpen: boolean = false;
  @State() isMenuClosing: boolean = false;

  /**
   * Event emitted when value changes
   */
  @Event() bhChange!: EventEmitter<MonthYear | null>;

  /**
   * Event emitted when a month/year is selected
   */
  @Event() bhMonthYearSelect!: EventEmitter<MonthYear>;

  @Watch('value')
  handleValueChange(newValue: MonthYear | undefined) {
    if (newValue !== undefined) {
      this.selectedValue = newValue;
    }
  }

  componentWillLoad() {
    if (this.value) {
      this.selectedValue = this.value;
    }
  }

  private getDisplayValue(): string {
    if (!this.selectedValue) return '';
    // US convention: MM/YYYY (01/2025)
    const month = (this.selectedValue.month + 1).toString().padStart(2, '0');
    return `${month}/${this.selectedValue.year}`;
  }

  private getButtonLabel(): string {
    return this.getDisplayValue() || this.placeholder;
  }

  private handleTriggerClick = () => {
    if (this.disabled) return;
    if (!this.isMenuOpen && this.showFooter) {
      this.tempSelection = this.selectedValue;
    }
    this.isMenuOpen = !this.isMenuOpen;
  };

  private handleInputFocus = () => {
    if (this.disabled) return;
    if (!this.isMenuOpen && this.showFooter) {
      this.tempSelection = this.selectedValue;
    }
    this.isMenuOpen = true;
  };

  private handleMonthYearSelect = (event: CustomEvent<MonthYear | null>) => {
    const monthYear = event.detail;
    if (!monthYear) return;
    if (this.showFooter) {
      this.tempSelection = monthYear;
    } else {
      this.selectedValue = monthYear;
      this.bhChange.emit(monthYear);
      this.bhMonthYearSelect.emit(monthYear);
      this.closeMenu();
    }
  };

  private handleMenuCancel = () => {
    this.tempSelection = null;
    this.closeMenu();
  };

  private handleMenuApply = () => {
    if (this.tempSelection) {
      this.selectedValue = this.tempSelection;
      this.bhChange.emit(this.tempSelection);
      this.bhMonthYearSelect.emit(this.tempSelection);
      this.tempSelection = null;
    }
    this.closeMenu();
  };

  private closeMenu() {
    this.isMenuClosing = true;
    setTimeout(() => {
      this.isMenuOpen = false;
      this.isMenuClosing = false;
    }, 150);
  }

  private handleBackdropClick = () => {
    this.handleMenuCancel();
  };

  render() {
    return (
      <Host>
        <div class="month-picker-container">
          {/* Dropdown Outlined Variant */}
          {this.variant === 'dropdown-outlined' && (
            <div class="month-picker-trigger" onClick={this.handleTriggerClick}>
              <bh-button
                hierarchy="secondary"
                
                size="sm"
                icon="trailing"
                label={this.getButtonLabel()}
                iconName="expand_more"
                disabled={this.disabled}
              ></bh-button>
            </div>
          )}

          {/* Dropdown Ghost Variant */}
          {this.variant === 'dropdown-ghost' && (
            <div class="month-picker-trigger" onClick={this.handleTriggerClick}>
              <bh-button
                hierarchy="tertiary"
                
                size="sm"
                icon="trailing"
                label={this.getButtonLabel()}
                iconName="expand_more"
                disabled={this.disabled}
              ></bh-button>
            </div>
          )}

          {/* Input Variant */}
          {this.variant === 'input' && (
            <div class="month-picker-trigger input-field-container">
              <bh-input-text
                label={this.label}
                placeholder={this.placeholder}
                value={this.getDisplayValue()}
                disabled={this.disabled}
                showTrailingIcon={true}
                trailingIcon="calendar_month"
                showHelpIcon={false}
                showHintText={false}
                showLabel={this.showLabel}
                width={this.inputWidth}
                onBhFocus={this.handleInputFocus}
              ></bh-input-text>
              <div class="calendar-icon-overlay" onClick={this.handleTriggerClick}></div>
            </div>
          )}

          {/* Month Picker Menu */}
          <div class="month-picker-menu-container">
            <bh-picker-menu
              visible={this.isMenuOpen}
              minYear={this.minYear}
              maxYear={this.maxYear}
              disabled={this.disabled}
              disabledMonths={this.disabledMonths}
              showFooter={this.showFooter}
              value={this.tempSelection || this.selectedValue || undefined}
              onBhChange={this.handleMonthYearSelect}
              onBhMonthYearSelect={this.handleMonthYearSelect}
              onBhCancel={this.handleMenuCancel}
              onBhApply={this.handleMenuApply}
            ></bh-picker-menu>
          </div>
        </div>

        {/* Backdrop */}
        {(this.isMenuOpen || this.isMenuClosing) && (
          <div class="month-picker-backdrop" onClick={this.handleBackdropClick}></div>
        )}
      </Host>
    );
  }
}
